@font-face {
  font-family: FrizQuadrata;
  src: url("Friz_Quadrata_Regular.34ad1b53.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

body {
  color: beige;
  background-color: #000;
  font-family: FrizQuadrata, sans-serif;
}

header {
  text-align: center;
  border-radius: 5px;
  align-items: center;
  padding: 10px 20px;
  font-size: 2em;
  font-weight: bolder;
  transition-duration: 2s;
}

header:hover {
  color: #000;
  background-color: beige;
  transition-duration: 2s;
  box-shadow: 0 0 10px beige;
}

.campeones {
  flex-wrap: wrap;
  justify-content: center;
  margin: 100px;
  display: flex;
}

.champion {
  color: beige;
  text-align: center;
  background-color: #000;
  border: 1px solid beige;
  border-radius: 10px;
  margin: 10px;
  padding: 10px;
  transition-duration: .6s;
  box-shadow: 0 0 10px beige;
}

.champion:hover {
  color: #000;
  background-color: beige;
  transition-duration: .6s;
  transform: scale(1.1);
  box-shadow: 0 0 10px #000;
}

.imagen {
  width: 308px;
  height: 560px;
}

.info {
  display: none;
}

.error {
  color: red;
  text-align: center;
  font-size: 1.5em;
}
